	import md5 from '../../static/js/md5.js';
	import rs from '../../static/js/request.js';
	let {
		log
	} = console;
	const app = getApp().globalData;
	const {
		appid,
		appsecret,
		imgRemote,
		navBar
	} = app;
	export default {
		data() {
			return {
				address: '',
				contact: '',
				details: '',
				mobile: '',
				childzid: '',
				navBar: navBar,
				lat: '',
				lng: '',
				count:0
			};
		},
		methods: {
			leftClick() {
				uni.switchTab({
					url: '../tabar/shopcart'
				})
			},
			mapPage() {
				let that = this;








				uni.chooseLocation({
					success: function(res) {
						// console.log('位置名称：' + res.name);
						that.address = res.address;
						that.lat = res.latitude;
						that.lng = res.longitude;
					}
				});
			},
			memberAddressInfo() {
				let timeStamp = Math.round(new Date().getTime() / 1000);
				let obj = {
					appid: appid,
					timeStamp: timeStamp
				};

				let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				let params = Object.assign({
					select_zid: this.childzid,
					sign: sign
				}, obj)
				rs.getRequests("memberAddressInfo", params, (res) => {
					// console.log(res)
					if (res.data.code == 200) {
						let data = res.data.data;
						this.contact = data.contact;
						this.mobile = data.phone;
						this.address = data.address;
						this.details = data.details;
					}
				})
			},
			submit() {
				var reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/;
				if (!this.contact) {
					rs.Toast('联系人不能为空');
					return;
				}
				if (!this.mobile) {
					rs.Toast('手机号不能为空');
					return;
				}
				if (!reg.test(this.contact)) {
					rs.Toast('输入的信息不能含有特殊字符和空格');
					return;
				}
				this.count++;
				if(this.count!=1)return;
				setTimeout(()=>{this.count=0},500)
				let timeStamp = Math.round(new Date().getTime() / 1000);
				let obj = {
					appid: appid,
					timeStamp: timeStamp,
					contact: this.contact,
					mobile: this.mobile,
					address: this.address,
				};
				let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
				let params = Object.assign({
					sign: sign,
					details: this.details,
					latitude: this.lat,
					longitude: this.lng
				}, obj)
				rs.postRequests("updateAddress", params, (res) => {
					if (res.data.code == 200) {
						rs.Toast('保存成功');
						setTimeout(() => {
							this.leftClick();
						}, 1000);

					} else {
						rs.Toast(res.data.msg);
					}

				})
			},
		},
		onLoad(option) {
			this.childzid = option.childzid;
			this.contact = option.contact;
			this.mobile = option.mobile;
			this.address = option.address;
			this.details = option.details;
			let count = option.count || 1;
			// console.log(option.count)
			if (count == 1) {
				
					this.memberAddressInfo()
			}
		},
onShow() {
	uni.setNavigationBarTitle({
		    title: uni.getStorageSync('titleKey')
		});
}


	};
